/* eslint-disable no-use-before-define */
import { Subscription } from 'rxjs';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';

import { Component, ElementRef, HostListener, Input, OnDestroy } from '@angular/core';
import { FormControl, NG_VALUE_ACCESSOR } from '@angular/forms';
import { DynamicListOption } from '@nequi/features/dynamic-form/core';
import { InputControlValue } from '@nequi/ui';

const DEFAULT_DEBOUNCE = 250;
const DEFAULT_MIN_LENGTH_TO_SEARCH = 3;
const removeSpaces = (text: string) => text?.trim();

@Component({
  selector: 'app-search-select',
  templateUrl: './search-select.component.html',
  styleUrls: ['./search-select.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: SearchSelectComponent,
    },
  ],
})
export class SearchSelectComponent extends InputControlValue implements OnDestroy {
  @Input() public placeholder: string;
  @Input() public hint: string;
  @Input() public emptyMessage: string;
  @Input() public options: (criteria: string) => Promise<DynamicListOption[]>;

  @Input() public debounce = DEFAULT_DEBOUNCE;
  @Input() public minLengthToSearch = DEFAULT_MIN_LENGTH_TO_SEARCH;

  public criteria = new FormControl('');
  public showSuggestions = false;
  public shouldShowEmptyMessage = false;
  public isLoading = false;
  public suggestions: DynamicListOption[];

  private isWriting = false;
  private criteriaSubscription: Subscription;

  constructor(private elementRef: ElementRef) {
    super();
  }

  @HostListener('document:click', ['$event'])
  public clickOut(event: Event): void {
    if (!this.elementRef.nativeElement.contains(event.target)) {
      this.closeSuggestions();
    }
  }

  public ngOnDestroy(): void {
    this.criteriaSubscription?.unsubscribe();
  }

  public get label(): string {
    return this.placeholder;
  }

  public get isFilled(): boolean {
    return !!this.value;
  }

  public get isHintVisible(): boolean {
    return (
      removeSpaces(this.criteria?.value)?.length < this.minLengthToSearch || !this.criteria?.value
    );
  }

  public get hasSuggestions(): boolean {
    return this.suggestions?.length > 0;
  }

  public get isSearchIconVisible(): boolean {
    return !this.isFilled && !this.isWriting;
  }

  public writeValue(value: DynamicListOption) {
    this.criteria.setValue(value?.title);
    super.writeValue(value);
  }

  public openSuggestions(): void {
    this.watchCriteriaChanges();
    setTimeout(() => {
      this.showSuggestions = true;
      this.isWriting = true;
    }, 1);
  }

  public closeSuggestions(): void {
    this.showSuggestions = false;
    this.isWriting = false;
  }

  public clearInput() {
    this.setValue(null);
    this.criteria.setValue('');
  }

  public onItemSelected(item: DynamicListOption) {
    this.criteriaSubscription?.unsubscribe();
    this.criteriaSubscription = null;
    this.criteria.setValue(item.title);

    this.setValue(item);
    this.closeSuggestions();
  }

  private watchCriteriaChanges(): void {
    if (this.criteriaSubscription) {
      return;
    }

    this.criteriaSubscription = this.criteria.valueChanges
      .pipe(debounceTime(this.debounce))
      .pipe(distinctUntilChanged())
      .subscribe(async (value) => this.onCriteriaHasChanges(value));
  }

  private async onCriteriaHasChanges(value: string): Promise<void> {
    if (removeSpaces(value)?.length >= this.minLengthToSearch) {
      this.isLoading = true;
      this.suggestions = await this.options(value);
      this.shouldShowEmptyMessage = true;
      this.isLoading = false;
    } else {
      this.shouldShowEmptyMessage = false;
      this.suggestions = [];
    }
  }
}
