import { FormControl, FormGroup } from '@angular/forms';
import {
  DynamicButton,
  DynamicField,
  ScreenConfiguration
} from '@nequi/features/dynamic-form/core';

import { FormField } from '../../entities/form-field';
import { ValidateInfoConfig } from './validate-info.config';

const sendEditableToEnd = (referenceA: FormField, referenceB: FormField): number =>
  Number(referenceA.field.isEditable) - Number(referenceB.field.isEditable);

export class ValidateInfoViewModel {
  public balanceToggle = false;
  public pocketList;
  public form: FormGroup;
  public pocket = new FormControl();
  public fields: FormField[];
  public viewProperties: ScreenConfiguration;

  private readonly config = ValidateInfoConfig;

  public get title(): string {
    return this.viewProperties?.title ?? this.config.i18n.validate.title;
  }

  public get subtitle(): string {
    return this.viewProperties?.subtitle ?? this.config.i18n.validate.subtitle;
  }

  public getButtonLabel(button: DynamicButton): string {
    return button.label ?? this.config.i18n.validate.buttonLabel;
  }

  public initForm(references: DynamicField[]) {
    this.fields = references.map((field) => {
      return {
        control: new FormControl({ value: field.value, disabled: field.isDisabled }),
        field,
      };
    }).sort(sendEditableToEnd);

    this.buildForm();
  }

  private buildForm() {
    this.form = new FormGroup({
      pocket: this.pocket,
      ...this.fields.reduce((fields, field) => {
        return { ...fields, [field.field.name]: field.control };
      }, {}),
    });
  }
}
