import { AbstractControl } from '@angular/forms';
import { Singleton } from '@nequi/commons/decorators';

@Singleton
export class PersistenceForm {
  private formIdentifier: string;
  private homeFormInfo: { [p: string]: AbstractControl };
  private validateInfo: { [p: string]: AbstractControl };

  public get formId(): string {
    return this.formIdentifier;
  }

  public set formId(value: string) {
    this.formIdentifier = value;
  }

  public get homeForm(): { [p: string]: AbstractControl } {
    return this.homeFormInfo;
  }

  public set homeForm(value: { [p: string]: AbstractControl }) {
    this.homeFormInfo = value;
  }

  public get validate(): { [p: string]: AbstractControl } {
    return this.validateInfo;
  }

  public set validate(value: { [p: string]: AbstractControl }) {
    this.validateInfo = value;
  }
}
