import { Injectable } from '@angular/core';
import { InformativeView } from '@nequi/features/dynamic-form/core';
import { BottomSheetController } from '@nequi/ui';

import {
  InformativeContentComponent
} from '../../components/dynamic-informative-view-content/informative-content.component';
import { BaseBottomSheetService } from './base-bottom-sheet.service';

@Injectable()
export class InformativeViewBottomSheetService
  implements BaseBottomSheetService<InformativeView, void> {
  constructor(private bottomSheetController: BottomSheetController) {}

  public async show(componentProps: InformativeView) {
    const bottomSheet = this.bottomSheetController.create({
      isAutoHeight: true,
      component: InformativeContentComponent,
      componentProps: {
        slides: componentProps.content,
        link: componentProps.link,
        button: componentProps.button,
        onDismiss: () => bottomSheet.close(),
      },
      cssClass: '',
    });

    await bottomSheet.open();
  }

  public hide() {
    return this.bottomSheetController.dismiss();
  }
}
