import { Observable } from 'rxjs';

import { Injectable } from '@angular/core';
import {
  ActivatedRouteSnapshot,
  CanActivate,
  Router,
  RouterStateSnapshot,
  UrlTree
} from '@angular/router';

import { DynamicListOrganism } from '../../organisms/dynamic-list/dynamic-list.organism';
import {
  DynamicListBottomSheetService
} from '../../services/bottom-sheet/dynamic-list-bottom-sheet.service';

@Injectable()
export class CategoriesMiddleware implements CanActivate {
  public tool;

  constructor(
    private router: Router,
    private dynamicListBottomSheetService: DynamicListBottomSheetService
  ) {}

  public canActivate(
    _route: ActivatedRouteSnapshot,
    _state: RouterStateSnapshot
  ): boolean | UrlTree | Observable<boolean | UrlTree> | Promise<boolean | UrlTree> {
    if (this.router.getCurrentNavigation().extras.queryParams) {
      this.tool = this.router.getCurrentNavigation().extras.queryParams.args;
      this.showCategories();

      return;
    }

    this.tool = this.router.getCurrentNavigation().extras.state;

    this.showCategories();

    return false;
  }

  private async showCategories(): Promise<void> {
    this.dynamicListBottomSheetService.show(
      {
        tool: this.tool,
      },
      DynamicListOrganism
    );
  }
}
