import { Component, Input, OnInit } from '@angular/core';
import { NavController } from '@ionic/angular';
import {
  DynamicFormInteractor,
  DynamicFormRepository,
  ProductEntity
} from '@nequi/features/dynamic-form/core';
import { BottomSheetController, LoadingService } from '@nequi/ui';

import { DynamicFormConfig } from '../../dynamic-form.config';
import {
  DynamicListBottomSheetService
} from '../../services/bottom-sheet/dynamic-list-bottom-sheet.service';
import { DynamicListExtras } from './entities/dynamic-list-props.interface';

@Component({
  selector: 'app-dynamic-list',
  templateUrl: './dynamic-list.organism.html',
  styleUrls: ['./dynamic-list.organism.scss'],
  providers: [
    {
      provide: DynamicFormInteractor,
      deps: [DynamicFormRepository],
      useClass: DynamicFormInteractor,
    },
  ],
})
export class DynamicListOrganism implements OnInit {
  @Input() public tool: DynamicListExtras;
  public isLoading: boolean;
  public loadingError: string;
  public items = [];

  constructor(
    public bottomSheetController: BottomSheetController,
    private dynamicFormInteractor: DynamicFormInteractor,
    private loadingService: LoadingService,
    private navController: NavController,
    private dynamicListBottomSheetService: DynamicListBottomSheetService
  ) {}

  public ngOnInit() {
    this.getCategoryList(this.tool.id);
  }

  public hide() {
    this.bottomSheetController.dismiss();
  }

  public reload() {
    this.getCategoryList(this.tool.id);
  }

  private async getCategoryList(categoryId: string) {
    try {
      this.isLoading = true;
      this.loadingError = '';
      const categoryList = await this.dynamicFormInteractor.getCategoryList(categoryId);

      this.isLoading = false;
      this.showCategories(categoryList);
    } catch (error) {
      this.loadingError = error.message;
      await this.loadingService.hide(false);
    }
  }

  private async showCategories(categories: ProductEntity[]): Promise<void> {
    const itemsCategory = categories.map((item) => {
      return {
        id: item.id,
        label: item.name,
        subtitle: item.subtitle ?? null,
        icon: item.icon.name ?? DynamicFormConfig.iconDefault,
        iconColor: item.icon.color,
        iconBackground: item.icon.background,
        action: async () => {
          await this.dynamicListBottomSheetService.hide();

          await this.navController.navigateForward(
            [item.goTo ?? DynamicFormConfig.routes.form, item.id],
            {
              state: item,
            }
          );
        },
        isIcon: true,
      };
    });

    this.items = itemsCategory;
  }
}
