import { CommonModule, CurrencyPipe, DatePipe } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { ApplicationDataInteractor } from '@nequi/features/application-data/core';
import { QrReaderService } from '@nequi/features/commons/presentation';
import { ModuleConfigProvider } from '@nequi/features/commons/providers';
import { FeaturesContactsModule } from '@nequi/features/contacts/presentation';
import { DynamicFormInteractor, DynamicFormRepository } from '@nequi/features/dynamic-form/core';
import { FeaturesMoneyManagementModule } from '@nequi/features/money-management/presentation';
import { ConfirmInfoBottomSheetService, NequiComponentsModule } from '@nequi/ui';
import { TranslateModule, TranslatePipe } from '@ngx-translate/core';

import { DynamicFormConfig } from '../config/dynamic-form.config';
import {
  InformativeContentComponent
} from './components/dynamic-informative-view-content/informative-content.component';
import { SelectorItemComponent } from './components/selector-item/selector-item.component';
import { DynamicInputComponent } from './controls/dynamic-input/dynamic-input.component';
import { SearchSelectComponent } from './controls/search-select/search-select.component';
import { DynamicFormRoutingModule, MapParamsToFormId } from './dynamic-form-routing.module';
import { DynamicListOrganism } from './organisms/dynamic-list/dynamic-list.organism';
import { ProductListOrganism } from './organisms/product-list/product-list.organism';
import { CategoriesMiddleware } from './pages/categories/categories.middleware';
import { HomePage } from './pages/home/home.page';
import { PersistenceForm } from './pages/persistence.form';
import { ValidateInfoPage } from './pages/validate-info/validate-info.page';
import {
  InformativeViewBottomSheetService
} from './services/bottom-sheet/informative-view-bottom-sheet.service';
import { DynamicFormService } from './services/dynamic-form.service';

const INTERACTOR_PROVIDER = {
  provide: DynamicFormInteractor,
  deps: [DynamicFormRepository, ApplicationDataInteractor, ModuleConfigProvider],
  useFactory: (
    repository: DynamicFormRepository,
    applicationDataInteractor: ApplicationDataInteractor,
    moduleConfigProvider: ModuleConfigProvider
  ) => new DynamicFormInteractor(repository, applicationDataInteractor, moduleConfigProvider),
};

const PERSISTENCE_PROVIDER = {
  provide: PersistenceForm,
  useFactory: () => new PersistenceForm(),
};

@NgModule({
  imports: [
    CommonModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule,
    DynamicFormRoutingModule,
    TranslateModule,
    FeaturesMoneyManagementModule,
    FeaturesContactsModule,
    NequiComponentsModule,
  ],
  declarations: [
    DynamicListOrganism,
    DynamicInputComponent,
    InformativeContentComponent,
    HomePage,
    ValidateInfoPage,
    ProductListOrganism,
    SearchSelectComponent,
    SelectorItemComponent,
  ],
  providers: [
    TranslatePipe,
    DatePipe,
    CurrencyPipe,
    QrReaderService,
    DynamicFormService,
    InformativeViewBottomSheetService,
    ConfirmInfoBottomSheetService,
    INTERACTOR_PROVIDER,
    MapParamsToFormId,
    CategoriesMiddleware,
    PERSISTENCE_PROVIDER,
    {
      provide: ModuleConfigProvider,
      useValue: DynamicFormConfig,
    },
  ],
})
export class DynamicFormModule {}
