import { Subscription } from 'rxjs';
import { distinctUntilChanged } from 'rxjs/operators';

/* eslint-disable no-use-before-define */
import { Component, Input, OnChanges, OnDestroy, OnInit, SimpleChanges } from '@angular/core';
import {
  FormControl,
  NG_VALIDATORS,
  NG_VALUE_ACCESSOR,
  ValidationErrors,
  Validator,
  ValidatorFn,
  Validators
} from '@angular/forms';
import {
  DynamicField,
  DynamicListOption,
  FieldType,
  FieldValidation,
  ValidationType
} from '@nequi/features/dynamic-form/core';
import { CurrencyConfigProvider, InputControlValue } from '@nequi/ui';

import { PresentationDynamicField } from '../../entities/form-field';

type ControlValidations = ValidatorFn | ValidatorFn[] | null;

const ValidationMapper: { [key: string]: Function } = {
  [ValidationType.REQUIRED]: Validators.required,
  [ValidationType.MIN]: Validators.min,
  [ValidationType.MAX]: Validators.max,
  [ValidationType.MIN_LENGTH]: Validators.minLength,
  [ValidationType.MAX_LENGTH]: Validators.maxLength,
  [ValidationType.PATTERN]: Validators.pattern,
};

const getNumberOfDecimals = (value: unknown): number => {
  const numberValue = Number(value);

  if (isNaN(numberValue)) {
    return 0;
  }

  if (numberValue % 1 !== 0) {
    return numberValue.toString().split('.')[1].length;
  }

  return 0;
};

const getValidationMessages = (
  messages: Record<string, string>,
  validation: FieldValidation
): Record<string, string> => {
  messages[validation.type] = validation.message;

  return messages;
};

const getValidations = (validations?: FieldValidation[]): ControlValidations =>
  validations
    ?.map((validation) => {
      if (validation.type === ValidationType.REQUIRED) {
        return ValidationMapper[validation.type];
      }

      return ValidationMapper[validation.type]?.(validation.value);
    })
    .filter((validation) => !!validation);

@Component({
  selector: 'app-dynamic-input',
  templateUrl: 'dynamic-input.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: DynamicInputComponent,
    },
    {
      provide: NG_VALIDATORS,
      multi: true,
      useExisting: DynamicInputComponent,
    },
  ],
})
export class DynamicInputComponent
  extends InputControlValue
  implements Validator, OnInit, OnChanges, OnDestroy {
  @Input() public field: PresentationDynamicField;
  @Input() public dependency: FormControl;

  public readonly FieldTypes = FieldType;
  public validationMessages: Record<string, string>;
  public infoMessage: string;
  public formControl: FormControl;
  private fieldConfigByDependency: DynamicField;
  private $dependencySubscription: Subscription;
  private $controlSubscription: Subscription;

  constructor(private currencyConfigProvider: CurrencyConfigProvider) {
    super();
  }

  public get fieldConfig(): DynamicField {
    return this.fieldConfigByDependency ?? this.field;
  }

  public get isHidden(): boolean {
    const hasDependency = !!this.dependency;
    const isInvalidDependencyValue = !this.dependency?.value && this.dependency?.invalid;

   return (
     (!hasDependency && !this.field.isVisible) ||
     (hasDependency && isInvalidDependencyValue && !this.field.isVisible) ||
     (hasDependency && this.field?.id &&
      this.field?.id !== this.dependency?.value?.value &&
      this.dependency?.value?.value?.length < 0)
   );
  }

  public get decimalLimit(): number {
    return Math.max(
      getNumberOfDecimals(this.field.value),
      this.currencyConfigProvider.decimalLimit,
      ...this.field.validations
        .filter(
          (validation) =>
            validation.type === ValidationType.MIN || validation.type === ValidationType.MAX
        )
        .map((validation) => getNumberOfDecimals(validation.value))
    );
  }

  // Override
  public ngOnInit(): void {
    this.formControl = new FormControl(null, getValidations(this.field.validations));
    this.validationMessages = this.fieldConfig?.validations.reduce(getValidationMessages, {
      info: this.field.toolTip,
    });

    this.watchControlChanges();
    this.watchDependencyChanges();
  }

  // Override
  public ngOnChanges(_changes: SimpleChanges): void {
    this.watchDependencyChanges();
  }

  // Override
  public ngOnDestroy(): void {
    this.$dependencySubscription?.unsubscribe();
    this.$controlSubscription?.unsubscribe();
  }

  // Override
  public writeValue(value) {
    this.value = value;
    this.formControl.setValue(value);
  }

  // Override
  public registerOnChange(fn) {
    this.formControl.registerOnChange(fn);
    this.onChange = fn;
  }

  // Override
  public setDisabledState(isDisabled: boolean): void {
    this.switchControlState(isDisabled);
  }

  // Override
  public validate(): ValidationErrors {
    return this.formControl.invalid && !this.isHidden && this.formControl.errors;
  }

  public getSelectOptions(searchCriteria?: string): Promise<DynamicListOption[]> {
    return this.field.options?.(this.field, searchCriteria);
  }

  private watchControlChanges() {
    if (this.$controlSubscription) {
      return;
    }

    this.$controlSubscription = this.formControl.valueChanges
      .pipe(distinctUntilChanged())
      .subscribe((value) => {
        this.onChange?.(value);
      });
  }

  private watchDependencyChanges() {
    this.switchControlState();

    if (!this.dependency || this.$dependencySubscription) {
      return;
    }

    this.$dependencySubscription = this.dependency.valueChanges
      .pipe(distinctUntilChanged())
      .subscribe(() => {
        this.switchControlState();
        this.formControl.setValue(null);

        if (this.field.configByDependency?.length > 0) {
          this.updateFieldInfo();
        }
      });
  }

  private updateFieldInfo(): void {
    if (this.field.configByDependency?.length > 0) {
      this.fieldConfigByDependency = {
        ...this.field,
        ...this.field.configByDependency.find(
          (config) => config.id === this.dependency.value.value
        ),
      };
    }

    this.formControl.setValidators(getValidations(this.fieldConfig?.validations));
    this.validationMessages = this.fieldConfig?.validations.reduce(getValidationMessages, {
      info: this.field.toolTip,
    });
  }

  private switchControlState(isDisabled = false): void {
    if ((!this.dependency && !isDisabled) || (!isDisabled && this.dependency?.valid)) {
      return this.formControl?.enable();
    }

    return this.formControl?.disable();
  }
}
