import { Injectable, NgModule, Type } from '@angular/core';
import {
  ActivatedRouteSnapshot,
  CanActivate,
  Router,
  RouterModule,
  RouterStateSnapshot,
  Routes,
  UrlTree
} from '@angular/router';
import { NavController } from '@ionic/angular';
import { I18nModuleResolver } from '@nequi/features/commons';

import { DynamicFormConfig } from '../config/dynamic-form.config';
import { CategoriesMiddleware } from './pages/categories/categories.middleware';
import { HomePage } from './pages/home/home.page';
import { ValidateInfoPage } from './pages/validate-info/validate-info.page';

@Injectable()
export class MapParamsToFormId implements CanActivate {
  constructor(private navController: NavController, private router: Router) {}

  public async canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Promise<UrlTree> {
    let formId = this.router.getCurrentNavigation().extras.queryParams?.formId;

    if (!formId) {
      formId = this.router.getCurrentNavigation().extras.queryParams?.args?.formId;
    }

    if (!formId) {
      formId = this.router.getCurrentNavigation().extras.state?.params?.formId;
    }

    return this.router.parseUrl(`${state.url.replace(/\?.+/, '')}/${formId}`);
  }
}

const routes: Routes = [
  {
    path: 'form',
    component: Type,
    canActivate: [MapParamsToFormId],
  },
  {
    path: 'categories',
    component: Type,
    canActivate: [CategoriesMiddleware],
  },
  {
    path: 'form/:id',
    component: HomePage,
  },
  {
    path: 'form/:id/validate',
    component: ValidateInfoPage,
  },
];

@NgModule({
  imports: [
    RouterModule.forChild(
      I18nModuleResolver.setup(routes, [
        DynamicFormConfig.i18n.moduleKey,
        DynamicFormConfig.i18n.contactsModuleKey,
      ])
    ),
  ],
  exports: [RouterModule],
})
export class DynamicFormRoutingModule {}
