import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { OperationType, Transaction } from '@nequi/features/commons/core';
import {
  BillEntity,
  DynamicButton,
  DynamicField,
  DynamicFormRepository,
  FlowScreens
} from '@nequi/features/dynamic-form/core';
import { OriginMoneyControlComponent } from '@nequi/features/money-management/presentation';

import { DynamicFormConfig } from '../../dynamic-form.config';
import { DynamicFormBasePage } from '../base-page';
import { ValidateInfoConfig } from './validate-info.config';
import { ValidateInfoViewModel } from './validate-info.view-model';

@Component({
  selector: 'app-validate-info',
  templateUrl: './validate-info.page.html',
  styleUrls: ['./validate-info.page.scss'],
})
export class ValidateInfoPage extends DynamicFormBasePage implements OnInit {
  @ViewChild(OriginMoneyControlComponent) public originMoneyControl: OriginMoneyControlComponent;
  public viewModel = new ValidateInfoViewModel();

  public screenName = FlowScreens.VALIDATE;

  public readonly config = ValidateInfoConfig;

  private bill: BillEntity;

  public get transactionValue(): number {
    return this.getTransactionValue(this.bill, this.viewModel.form);
  }

  public async ngOnInit() {
    this.loadViewProperties();
  }

  public get minAmount() {
    return this.viewModel.form.get(this.config.referenceAmount)?.value || 0;
  }

  public onSubmit() {
    super.executeTransaction(this.getTransactionInfo(this.bill, this.viewModel.form));
  }

  public onGoBack() {
    this.navigatorProvider.back();
  }

  public onGoNext() {
    this.executeTransaction(this.getTransactionInfo(this.bill, this.viewModel.form));
  }

  public onScan() {
    // Do nothing
  }

  public onGoFirst() {
    this.navigatorProvider.navigateBack([DynamicFormConfig.routes.admin]);
  }

  public onButtonClicked(button: DynamicButton) {
    this.handleButtonAction(button, this.bill.formId);
  }

  private async loadViewProperties() {
    this.bill = this.router.getCurrentNavigation().extras.state as BillEntity;
    const updatedBill = await this.updateFormValues(this.bill);

    this.viewModel.viewProperties = await this.dynamicFormInteractor.getScreenConfiguration(
      FlowScreens.VALIDATE,
      this.bill.formId
    );

    this.viewModel.initForm(updatedBill.fields);

    if (this.viewModel.viewProperties.toast) {
      await this.dynamicFormService.showToast(
        this.viewModel.viewProperties,
        FlowScreens.VALIDATE,
        this.bill.formId
      );
    }
  }

  private async updateFormValues(bill: BillEntity) {
    const persistentData = await this.applicationDataInteractor.findValue<any>(
      this.moduleConfigProvider.applicationData.persistenceValidateDataKey + bill.formId
    );

    if (persistentData) {
      bill.fields.map((field) => {
        if (
          persistentData[field.name] &&
          field.isEditable &&
          field.name !== ValidateInfoConfig.referencesName.value
        ) {
          field.value = persistentData[field.name];
        }

        return field;
      });
    }

    return bill;
  }

  private getTransactionInfo(bill: BillEntity, form: FormGroup): Transaction<BillEntity> {
    return new Transaction<BillEntity>(bill.operationType as OperationType)
      .withAmount(this.getTransactionValue(bill, form))
      .withPocket(this.viewModel.pocket.value)
      .withData({
        ...bill,
        fields: bill.fields.map((field) => this.updateFieldValue(field, form)),
      })
      .useRepository(DynamicFormRepository.name);
  }

  private updateFieldValue(field: DynamicField, form: FormGroup): DynamicField {
    return {
      ...field,
      value: form.get(field.name).value,
    };
  }
}
