import { CurrencyPipe } from '@angular/common';
import { Component, Injector } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { CoreException, ExceedCeilingException } from '@nequi/commons/exceptions';
import { ApplicationDataInteractor } from '@nequi/features/application-data/core';
import {
  AnalyticsInteractor,
  LoggerInteractor,
  NOT_BALANCE_AVAILABLE_ERRORS,
  Transaction
} from '@nequi/features/commons/core';
import {
  AlertService,
  QrReaderService,
  TransactionBasePage
} from '@nequi/features/commons/presentation';
import { ModuleConfigProvider, TranslateProvider } from '@nequi/features/commons/providers';
import {
  ActionType,
  BillEntity,
  DynamicAlert,
  DynamicButton,
  DynamicField,
  DynamicFormInteractor,
  FieldType,
  FlowScreens
} from '@nequi/features/dynamic-form/core';
import { CardModalService, ConfirmInfoItem, ConfirmInfoPocketProps } from '@nequi/ui';
import { openUrl } from '@nequi/utils/functions';

import { DynamicFormConfig } from '../dynamic-form.config';
import {
  InformativeViewBottomSheetService
} from '../services/bottom-sheet/informative-view-bottom-sheet.service';
import { DynamicFormService } from '../services/dynamic-form.service';
import { PersistenceForm } from './persistence.form';
import { ValidateInfoConfig } from './validate-info/validate-info.config';

@Component({
  selector: 'app-dynamic-form-base',
  template: 'no-ui',
})
export abstract class DynamicFormBasePage extends TransactionBasePage {
  public abstract screenName: FlowScreens;

  constructor(
    private readonly cardModalService: CardModalService,
    public readonly alertService: AlertService,
    public readonly dynamicFormInteractor: DynamicFormInteractor,
    public readonly route: ActivatedRoute,
    public readonly router: Router,
    public readonly dynamicFormService: DynamicFormService,
    public readonly dynamicInformativeViewBottomSheetService: InformativeViewBottomSheetService,
    public readonly qrReaderService: QrReaderService,
    public readonly loggerInteractor: LoggerInteractor,
    public readonly translateProvider: TranslateProvider,
    public readonly moduleConfigProvider: ModuleConfigProvider,
    private readonly currencyPipe: CurrencyPipe,
    public analyticsInteractor: AnalyticsInteractor,
    protected readonly applicationDataInteractor: ApplicationDataInteractor,
    protected persistenceForm: PersistenceForm,
    injector: Injector
  ) {
    super(injector);
    this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey);
  }

  public abstract onSubmit(button: DynamicButton);
  public abstract onGoBack();
  public abstract onGoNext(button: DynamicButton);
  public abstract onScan(button: DynamicButton);
  public abstract onGoFirst(button: DynamicButton);

  // eslint-disable-next-line complexity
  public handleButtonAction(button: DynamicButton, formId: string) {
    if (
      button.actionInfo?.goTo?.includes(DynamicFormConfig.routes.oldHomeRoute) &&
      !button.params
    ) {
      return;
    }

    switch (button?.action) {
      case ActionType.ALERT:
        this.handleAlertInformation(button.actionInfo?.alert, formId);
        break;
      case ActionType.GO_TO_LINK:
        openUrl(button.actionInfo?.url);

        if (button.actionInfo?.redirect) {
          this.navigatorProvider.navigateForward(button.actionInfo?.redirect);
        }

        break;
      case ActionType.SUBMIT:
        this.onSubmit(button);
        break;
      case ActionType.CAMERA:
        this.onScan(button);
        break;
      case ActionType.GO_BACK:
        this.onGoBack();
        break;
      case ActionType.GO_TO:
        this.navigatorProvider.navigateForward(button.actionInfo.goTo);
        break;
      case ActionType.GO_NEXT:
        this.onGoNext(button);
        break;
      case ActionType.GO_ADMIN:
        return this.goAdmin();
      case ActionType.GO_FIRST:
        this.onGoFirst(button);
        break;
      case ActionType.GO_BREAK_CEILING:
        // TODO: Agregar redireccion cuando se implemente
        break;
      default:
        break;
    }
  }

  public async handleAlertInformation(
    alertInfo: DynamicAlert | string,
    formId: string,
    screenName = this.screenName,
    backButtonAction?: () => void
  ): Promise<void> {
    if (!alertInfo) {
      return;
    }

    let alert: DynamicAlert;

    if (typeof alertInfo === 'string') {
      alert = await this.dynamicFormInteractor.getAlert(alertInfo as string, screenName, formId);
    } else {
      alert = alertInfo as DynamicAlert;
    }

    if (!alert) {
      return;
    }

    if (alert.type === 'modal') {
      await this.cardModalService.show({
        imgPath: alert.image ?? DynamicFormConfig.defaultSuccessAlertAsset,
        title: alert.title,
        content: alert.content,
        nativeBackButtonAction: backButtonAction,
        buttons: alert.buttons.map((button, index) => {
          return {
            text: button.label,
            handler: () => this.handleButtonAction(button, formId),
            color: index === 0 ? 'guayaba' : 'medianoche-gray',
          };
        }),
      });
    } else {
      const buttons = {
        accept: alert.buttons[0]?.label,
        cancel: alert.buttons[1]?.label ?? undefined,
      };

      if (alert.buttons.length === 1) {
        delete buttons.cancel;
      }

      this.alertService.show({
        messages: {
          header: alert.title,
          message: alert.content,
          buttons: buttons,
        },
        handlers: {
          cancel: () => this.handleButtonAction(alert.buttons[1], formId),
          accept: () => this.handleButtonAction(alert.buttons[0], formId),
        },
      });
    }
  }

  public getTransactionValue(bill: BillEntity, form: FormGroup): number {
    const field = bill.fields.find((currentField) => this.isValueReference(currentField));

    return Number(form.get(field.name).value);
  }

  public async getConfirmationInfo(
    transactionInfo: Transaction<BillEntity>
  ): Promise<ConfirmInfoPocketProps> {
    const screenConfig = await this.dynamicFormInteractor.getScreenConfiguration(
      FlowScreens.CONFIRM,
      transactionInfo.data.formId
    );

    const messages = this.translateProvider.get(ValidateInfoConfig.i18n.confirm);

    return {
      title: messages.title,
      subtitle: screenConfig.subtitle ?? messages.subtitle,
      items: this.getConfirmItems(transactionInfo.data),
      confirmLabel: screenConfig.buttons[0]?.label ?? messages.button,
      viewCancel: false,
    };
  }

  protected async setOriginMoney(transaction: Transaction) {
    if (transaction.pocket === null) {
      const pocket = this.originMoneyService.getAvailablePocket();

      transaction.withPocket(pocket);
    }

    return transaction;
  }

  protected async unhandledError(error: any) {
    if (this.loadingService.isShown) {
      await this.loadingService.hide(false);
    }

    this.loggerInteractor.error(JSON.stringify(error));

    if (
      error instanceof ExceedCeilingException ||
      NOT_BALANCE_AVAILABLE_ERRORS.includes(error.code)
    ) {
      throw error;
    }

    this.toastService.showError(error.message);
  }

  protected async onTransactionFailed(error: CoreException, transaction: Transaction<BillEntity>) {
    this.loadingService.hide(false);

    const errorPaymentAlert = await this.dynamicFormInteractor.getErrorAlert(
      transaction.data.formId,
      error.code
    );

    if (errorPaymentAlert) {
      this.handleAlertInformation(errorPaymentAlert, transaction.data.formId);

      return;
    }

    this.unhandledError(error);
  }

  protected async onTransactionSuccess(
    response?: Record<string, string>,
    transaction?: Transaction<BillEntity>
  ) {
    const successPaymentAlert = await this.dynamicFormInteractor.getSuccessPaymentAlert(
      transaction.data.formId,
      response
    );
    const configTagSync = transaction.data.tags?.successPayment.sync;
    const configTagAsync = transaction.data.tags?.successPayment.async;

    if (successPaymentAlert?.alert?.cache) {
      this.saveForm();
    }

    if (successPaymentAlert) {
      this.handleAlertInformation(successPaymentAlert.alert, transaction.data.formId, null, () =>
        this.goAdmin()
      );

      this.setAnalyticsSyncSuccessPayment(configTagSync);
    } else {
      // TODO: modificar logica para controlar redireccion a la pantalla donde el flujo inicio(backpack o closet)
      this.navigatorProvider.navigateForward(ValidateInfoConfig.routes.backpack);

      this.setAnalyticsAsyncSuccessPayment(configTagAsync);
    }
  }

  private goAdmin() {
    this.navigatorProvider.navigateRoot(DynamicFormConfig.routes.admin);
  }

  private saveForm() {
    if (!this.persistenceForm?.validate?.value) {
      delete this.persistenceForm?.validate?.value;
    }

    this.applicationDataInteractor.save(
      this.moduleConfigProvider.applicationData.persistenceHomeDataKey +
        this.persistenceForm.formId,
      this.persistenceForm.homeForm
    );

    this.applicationDataInteractor.save(
      this.moduleConfigProvider.applicationData.persistenceValidateDataKey +
        this.persistenceForm.formId,
      this.persistenceForm.validate
    );
  }

  private getConfirmItems(bill: BillEntity): ConfirmInfoItem[] {
    const referenceValue = this.getValueReference(bill);

    const references = bill.fields
      .filter((field) => field.isVisible && !this.isValueReference(field) && field.value)
      .map<ConfirmInfoItem>((field) => this.getConfirmInfoItem(field));

    references.push(this.getConfirmInfoItem(referenceValue));

    return references;
  }

  private getValueReference(bill: BillEntity): DynamicField {
    return bill.fields.find((currentField) => this.isValueReference(currentField));
  }

  private isValueReference(field: DynamicField) {
    return field.name === ValidateInfoConfig.referenceAmount;
  }

  private getConfirmInfoItem(field: DynamicField): ConfirmInfoItem {
    const value = field.value?.toString?.();

    return {
      label: field.complementaryLabel ?? field.placeHolder,
      value: field.type === FieldType.MONEY ? this.currencyPipe.transform(value) : value,
    };
  }

  private setAnalyticsSyncSuccessPayment(configTagSync) {
    if (!configTagSync) {
      return;
    }

    this.analyticsInteractor.trackEvent({
      type: configTagSync.facebook.type,
      params: {
        eventName: configTagSync.facebook.params.eventName,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: configTagSync.firebase.type,
      params: {
        eventName: configTagSync.firebase.params.eventName,
      },
    });
  }

  private setAnalyticsAsyncSuccessPayment(configTagAsync) {
    if (!configTagAsync) {
      return;
    }

    this.analyticsInteractor.trackEvent({
      type: configTagAsync.facebook.type,
      params: {
        eventName: configTagAsync.facebook.params.eventName,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: configTagAsync.firebase.type,
      params: {
        eventName: configTagAsync.firebase.params.eventName,
      },
    });
  }
}
