import { Injectable, Type } from '@angular/core';
import { BottomSheetController } from '@nequi/ui';

import {
  DynamicListProps
} from '../../organisms/dynamic-list/entities/dynamic-list-props.interface';
import {
  ProductListProps
} from '../../organisms/product-list/entities/product-list-props.interface';
import { BaseBottomSheetService } from './base-bottom-sheet.service';

@Injectable({ providedIn: 'root' })
export class DynamicListBottomSheetService
  implements BaseBottomSheetService<DynamicListProps | ProductListProps, boolean> {
  constructor(private bottomSheetController: BottomSheetController) {}

  public async show<T>(componentProps: T, organism: Type<unknown>) {
    const bottomSheet = this.bottomSheetController.create({
      isAutoHeight: true,
      component: organism,
      componentProps,
    });

    await bottomSheet.open();

    return bottomSheet.onDidDismiss();
  }

  public async hide() {
    return this.bottomSheetController.dismiss();
  }
}
