import { Component, Input } from '@angular/core';
import { BottomSheetController } from '@nequi/ui';

import { ProductListItem } from './entities/product-list-item.interface';
import { ProductListProps } from './entities/product-list-props.interface';

@Component({
  selector: 'app-product-list',
  templateUrl: './product-list.organism.html',
  styleUrls: ['./product-list.organism.scss'],
})
export class ProductListOrganism implements ProductListProps {
  @Input() public items: ProductListItem[];
  @Input() public title: string;

  constructor(public bottomSheetController: BottomSheetController) {}

  public hide() {
    this.bottomSheetController.dismiss();
  }
}
