import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { OperationType, Transaction } from '@nequi/features/commons/core';
import {
  ActionType,
  AnalyticsWhen,
  BillEntity,
  DynamicButton,
  DynamicForm,
  DynamicFormRepository,
  DynamicListOption,
  FieldType,
  FlowScreens,
  InformativeView,
  ProductEntity,
  ScreenConfiguration
} from '@nequi/features/dynamic-form/core';
import { OriginMoneyControlComponent } from '@nequi/features/money-management/presentation';
import { stringsArrayToObjectWithValues } from '@nequi/utils/functions';

import { DynamicFormConfig } from '../../dynamic-form.config';
import { FormField, PresentationDynamicField } from '../../entities/form-field';
import { DynamicFormBasePage } from '../base-page';
import { ValidateInfoConfig } from '../validate-info/validate-info.config';
import { HomeConfig } from './home.config';
import { HomeViewModel } from './home.view-model';

@Component({
  selector: 'app-dynamic-form-home',
  templateUrl: './home.page.html',
  styleUrls: ['./home.page.scss'],
})
export class HomePage extends DynamicFormBasePage implements OnInit, OnDestroy {
  @ViewChild(OriginMoneyControlComponent) public originMoneyControl: OriginMoneyControlComponent;

  public screenName: FlowScreens = FlowScreens.HOME;

  public viewModel = new HomeViewModel();
  public isLoading: boolean;
  public loadingError: string;
  public readonly config = HomeConfig;
  public showOriginMoney = true;

  private formId: string;
  private bills: BillEntity[];

  public isSubmitButton(button: DynamicButton): boolean {
    return button.action === ActionType.SUBMIT;
  }

  public onSubmit() {
    const params = this.viewModel.fields.reduce(this.getReferences.bind(this), {});

    this.getPendingBills(params);
  }

  public onGoBack() {
    this.navigatorProvider.back();
  }

  public onGoNext() {
    this.navigateToNext(this.bills);
  }

  public onScan(button: DynamicButton) {
    this.openScanner(button);
  }

  public onGoFirst() {
    this.goBack();
  }

  public getTransactionValue(bill?: BillEntity): number {
    if (!bill) {
      return 0;
    }

    const field = bill.fields.find(
      (currentField) => currentField.name === ValidateInfoConfig.referenceAmount
    );

    return Number(field.value);
  }

  public ngOnInit() {
    this.formId =
      this.route.snapshot.paramMap.get('id') ??
      (this.router.getCurrentNavigation().extras.state?.product as ProductEntity).id;

    this.persistenceForm.formId = this.formId;

    this.getFormInfo();
  }

  public async ngOnDestroy() {
    await this.toastService.hide();
  }

  public get minAmount() {
    const controlName = this.viewModel.fields.find(
      (field) => field?.field.type === this.config.referenceAmount
    )?.field?.name;

    return controlName ? this.viewModel.form.get(controlName)?.value : 0;
  }

  public async getSelectorOptions(
    field: PresentationDynamicField,
    searchCriteria?: string
  ): Promise<DynamicListOption[]> {
    if (
      field.fieldDependency &&
      this.viewModel.form.value?.[field.fieldDependency]?.value.length > 0 &&
      this.viewModel.form.value?.[field.fieldDependency]?.value?.[0].listName
    ) {
      field.config.listName = this.viewModel.form.value?.[
        field.fieldDependency
      ]?.value?.[0].listName;
    }

    const formParams = field.params?.reduce((currentParams, param) => {
      currentParams[param] = this.viewModel.form.value?.[param];

      return currentParams;
    }, {});

    return this.dynamicFormInteractor.getListOptions(
      field.config.listName,
      formParams,
      searchCriteria
    );
  }

  public onButtonClicked(button: DynamicButton) {
    this.handleButtonAction(button, this.formId);
  }

  public async getFormInfo(): Promise<void> {
    this.loadingError = '';
    this.isLoading = true;

    try {
      const formInfo = await this.dynamicFormInteractor.getProductInfo(this.formId);
      const screenConfig = await this.getScreenConfig(FlowScreens.HOME);
      const screenConfigValidate = await this.getScreenConfig(FlowScreens.VALIDATE);

      this.showOriginMoney = !!screenConfigValidate.complementaryConfiguration?.hideView;

      await this.onGetFormInfoSuccess(formInfo, screenConfig);
    } catch (error) {
      this.loadingError = error.message;
    } finally {
      this.isLoading = false;
    }
  }

  public async onGetFormInfoSuccess(form: DynamicForm, screenConfig: ScreenConfiguration) {
    this.viewModel.fieldOptionsFunction = this.getSelectorOptions.bind(this);
    const updatedForm = await this.updateFormValues(form);

    this.viewModel.initForm(updatedForm, screenConfig);

    const canShowInformativeView = await this.dynamicFormInteractor.mustShowInformativeView(
      screenConfig.informativeView.frequency,
      form.id
    );

    if (canShowInformativeView) {
      await this.showInformativeView(screenConfig.informativeView);
    }

    if (screenConfig.toast) {
      await this.dynamicFormService.showToast(screenConfig, FlowScreens.HOME, this.formId);
    }

    this.setDynamicAnalyticsOfIntention(form, AnalyticsWhen.INITIAL_INTENTION);
  }

  private async updateFormValues(form: DynamicForm) {
    const persistentData = await this.applicationDataInteractor.findValue<any>(
      this.moduleConfigProvider.applicationData.persistenceHomeDataKey + this.formId
    );

    if (!form?.cache && persistentData) {
      await this.applicationDataInteractor.remove(
        this.moduleConfigProvider.applicationData.persistenceHomeDataKey + this.formId
      );

      return form;
    }

    if (persistentData) {
      form.boxes.map((box) =>
        box.fields.map((field) => {
          if (persistentData[field.name]) {
            field.value = persistentData[field.name];
          }

          return field;
        })
      );
    }

    return form;
  }

  private getReferences(items: Record<string, unknown>, control: FormField) {
    if (control.field.type === FieldType.CONTACT) {
      const reference = control.field.name.replace(this.config.replace.regex, (_number) =>
        (parseInt(_number, 10) + 1).toString()
      );

      return {
        [control.field.name]: this.viewModel.form.get(control.field.name).value?.phoneNumber,
        [reference]: this.viewModel.form.get(control.field.name).value?.completeName,
      };
    }

    return {
      ...items,
      [control.field.name]:
        control.field.type === FieldType.SELECT || control.field.type === FieldType.SEARCH
          ? this.viewModel.form.get(control.field.name).value?.value
          : this.viewModel.form.get(control.field.name).value,
    };
  }

  private async showInformativeView(informativeView: InformativeView) {
    await this.dynamicInformativeViewBottomSheetService.show(informativeView);
  }

  private async openScanner(dynamicButtonActionInformation: DynamicButton) {
    try {
      const scannerView = dynamicButtonActionInformation.actionInfo.scannerView;
      const scannerButton = scannerView.button;

      const button = {
        icon: scannerButton?.icon ?? '',
        code: scannerButton?.code ?? '',
        name: scannerButton?.name ?? '',
      };

      const qrString = await this.qrReaderService.launch(
        {
          content: scannerView?.content,
          imageName: '',
          title: scannerView?.title,
          showGalleryButton: scannerView?.showGalleryButton.toString(),
        },
        button
      );

      if (!qrString) {
        return;
      }

      this.processBarcode(qrString, dynamicButtonActionInformation);
    } catch (error) {
      if (error !== this.config.errors.goBackOnCamera) {
        this.handleError(error);
      }
    }
  }

  private async processBarcode(barcode: string, button: DynamicButton) {
    try {
      await this.loadingService.show();
      const pendingBills = await this.dynamicFormInteractor.processBarcode(this.formId, barcode);

      await this.getPendingBills(pendingBills, button);
    } catch (error) {
      await this.loadingService.hide(false);
      this.handleError(error);
    }
  }

  private async handleError(error: any) {
    const alertConfig = await this.dynamicFormInteractor.getErrorAlert(this.formId, error.code);

    if (alertConfig) {
      await this.loadingService.hide(true);
      this.handleAlertInformation(alertConfig, this.formId);

      return;
    }

    await this.unhandledError(error);
  }

  private async getPendingBills(
    params: Record<string, unknown>,
    barcodeButton?: DynamicButton
  ): Promise<void> {
    const form = this.viewModel.formInfo;
    const formId = form.id;

    await this.loadingService.show();

    this.setDynamicAnalyticsOfIntention(form, AnalyticsWhen.QUERY_INTENTION);

    try {
      this.bills = await this.dynamicFormInteractor.getPendingBills(formId, params);

      this.onGetPendingBillsSuccess(this.bills);
    } catch (error) {
      if (barcodeButton) {
        await this.onGetPendingBillsFailFromBarcodeScanning(barcodeButton);

        return;
      }

      this.handleError(error);
    }
  }

  private async onGetPendingBillsSuccess(billsResponse: BillEntity[]) {
    await this.loadingService.hide(true);

    if (
      this.mustShowSubmitAlert(this.viewModel.screenConfig) ||
      billsResponse[0].showBeforeGoToNextAlert
    ) {
      this.handleAlertInformation('beforeGoToNextAlert', this.formId);
    } else {
      this.defineSubmitAction(billsResponse);
    }
  }

  private async defineSubmitAction(billsResponse: BillEntity[]) {
    const screenConfig = await this.getScreenConfig(FlowScreens.VALIDATE);

    this.persistenceForm.homeForm = this.viewModel.form.value;

    if (screenConfig.complementaryConfiguration?.hideView) {
      const bill = billsResponse[0];

      this.executeTransaction(this.getTransactionInfo(bill));
    } else {
      this.navigateToNext(billsResponse);
    }
  }

  private getTransactionInfo(bill: BillEntity): Transaction<BillEntity> {
    return new Transaction<BillEntity>(bill.operationType as OperationType)
      .withAmount(this.getTransactionValue(bill))
      .withPocket(this.viewModel.pocketControl.value)
      .withData({
        ...bill,
        fields: bill.fields,
      })
      .useRepository(DynamicFormRepository.name);
  }

  private async navigateToNext(bills: BillEntity[]) {
    const screenConfig = await this.getScreenConfig(FlowScreens.PRODUCTS);

    if (bills.length === 1) {
      this.navigatorProvider.navigateForward(
        [DynamicFormConfig.routes.form, this.formId, DynamicFormConfig.routes.validate],
        {
          state: bills[0],
        }
      );
    } else {
      this.dynamicFormService.showProductList(screenConfig.title, bills);

      if (screenConfig.toast) {
        await this.dynamicFormService.showToast(screenConfig, FlowScreens.PRODUCTS, this.formId);
      }
    }
  }

  private async onGetPendingBillsFailFromBarcodeScanning(button: DynamicButton) {
    this.loadingService.hide(false);

    const messages = this.translateProvider.get(this.config.i18n.homeGetPendingBillRetryAlert);

    await this.alertService.show({
      messages: {
        header: messages.title,
        message: messages.content,
        buttons: {
          cancel: messages.cancelButton,
          accept: messages.actionButton,
        },
      },
      handlers: {
        cancel: () => {},
        accept: () => {
          this.onScan(button);
        },
      },
    });
  }

  private mustShowSubmitAlert(screenConfiguration: ScreenConfiguration) {
    let hasAlertConfig = false;

    screenConfiguration.buttons.forEach((button) => {
      hasAlertConfig =
        screenConfiguration?.complementaryConfiguration?.beforeGoToNextAlert &&
        this.isSubmitButton(button);
    });

    return hasAlertConfig;
  }

  private async getScreenConfig(flow: FlowScreens) {
    return this.dynamicFormInteractor.getScreenConfiguration(flow, this.formId);
  }

  private setDynamicAnalyticsOfIntention(form: DynamicForm, tagType: string) {
    let tag = AnalyticsWhen.INITIAL_INTENTION;
    let posTag = 0;

    if (tagType === AnalyticsWhen.QUERY_INTENTION) {
      tag = AnalyticsWhen.QUERY_INTENTION;
      posTag = 1;
    }

    if (form.tags) {
      const tagToSend = Array.isArray(form?.tags) ? form.tags[posTag][tag] : form.tags[tag];

      if (tagToSend) {
        this.dynamicFormInteractor.processTagsParams({
          event: tagToSend.clevertap,
          commerce: form.id,
        });

        this.setAnalyticsIntention(tagToSend);
      }
    }
  }

  private setAnalyticsIntention(configTag) {
    const property = [];
    const valueProperty = [];
    const paramsLength = configTag.clevertap.params.eventProps.length;

    for (let i = 0; i < paramsLength; i++) {
      property.push(configTag.clevertap.params.eventProps[i].label);
      valueProperty.push(configTag.clevertap.params.eventProps[i].value);
    }

    this.analyticsInteractor.trackEvent({
      type: configTag.facebook.type,
      params: {
        eventName: configTag.facebook.params.eventName,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: configTag.firebase.type,
      params: {
        eventName: configTag.firebase.params.eventName,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: configTag.clevertap.type,
      params: {
        eventName: configTag.clevertap.params.eventName,
        eventProps: stringsArrayToObjectWithValues(property, valueProperty),
      },
    });
  }
}
