import { FormControl, FormGroup } from '@angular/forms';
import {
  DynamicButton,
  DynamicField,
  DynamicForm,
  DynamicFormBox,
  DynamicListOption,
  ScreenConfiguration
} from '@nequi/features/dynamic-form/core';

import { FormField } from '../../entities/form-field';
import { HomeConfig } from './home.config';

interface PresentationBox {
  name: string;
  description?: string;
  fields: FormField[];
}

const mapField = (field, optionsHandler) => {
  return {
    control: new FormControl(field.value),
    field: {
      ...field,
      options: optionsHandler,
    },
  };
};

const mapBox = (
  box: DynamicFormBox,
  optionsHandler: (field: DynamicField) => Promise<unknown[]>
): PresentationBox => {
  return {
    ...box,
    fields: box.fields.map((field) => mapField(field, optionsHandler)),
  };
};

const setFieldControl = (
  fields: Record<string, FormControl>,
  field: FormField
): Record<string, FormControl> => {
  fields[field.field.name] = field.control;

  return fields;
};

export class HomeViewModel {

  public form: FormGroup;
  public formInfo: DynamicForm;
  public screenConfig: ScreenConfiguration;
  public fields: FormField[];
  public fieldOptionsFunction: (field: DynamicField, criteria?: string) => Promise<DynamicListOption[]>;
  public boxes: PresentationBox[];
  public balanceToggle = false;
  public pocketControl = new FormControl();

  private config = HomeConfig;

  public get title() {
    return this.screenConfig?.title;
  }

  public getButtonLabel(button: DynamicButton): string {
    return button.label ?? this.config.i18n.buttonLabel;
  }

  public initForm(formInfo: DynamicForm, screenConfig: ScreenConfiguration): void {
    this.formInfo = formInfo;
    this.screenConfig = screenConfig;
    this.boxes = formInfo.boxes.map((box) => mapBox(box, this.fieldOptionsFunction));
    this.fields = this.boxes.reduce((fields, box) => [...fields, ...box.fields], []);
    this.form = new FormGroup(
         {
          pocket: this.pocketControl,
          ...this.fields.reduce(setFieldControl, {}),
        }
    );
  }
}
