import { Injectable } from '@angular/core';
import { NavigatorProvider } from '@nequi/features/commons';
import { AlertService } from '@nequi/features/commons/presentation';
import {
  ActionType,
  BillEntity,
  DynamicAlert,
  DynamicButton,
  DynamicFormInteractor,
  FlowScreens,
  ProductEntity,
  ScreenConfiguration
} from '@nequi/features/dynamic-form/core';
import { CardModalService, ToastService, ToastType } from '@nequi/ui';
import { ToastPosition } from '@nequi/ui/services';
import { openUrl } from '@nequi/utils/functions';

import { DynamicFormConfig } from '../dynamic-form.config';
import { ProductListItem } from '../organisms/product-list/entities/product-list-item.interface';
import { ProductListProps } from '../organisms/product-list/entities/product-list-props.interface';
import { ProductListOrganism } from '../organisms/product-list/product-list.organism';
import { DynamicListBottomSheetService } from './bottom-sheet/dynamic-list-bottom-sheet.service';

@Injectable()
export class DynamicFormService {
  public categoryList: ProductEntity[];

  private showedToasts = [];

  constructor(
    private dynamicListBottomSheetService: DynamicListBottomSheetService,
    private dynamicFormInteractor: DynamicFormInteractor,
    private navController: NavigatorProvider,
    private cardModalService: CardModalService,
    private alertService: AlertService,
    private toastService: ToastService
  ) {}

  public resetShowedToasts() {
    this.showedToasts = [];
  }

  /**
   * @returns The button when the button action can`t be resolved
   */
  public async showAlertInfo(
    alertInfo: DynamicAlert | string,
    screen: FlowScreens,
    formId: string
  ): Promise<DynamicButton> {
    if (!alertInfo) {
      return;
    }

    let alert: DynamicAlert;

    if (typeof alertInfo === 'string') {
      alert = await this.dynamicFormInteractor.getAlert(alertInfo as string, screen, formId);
    } else {
      alert = alertInfo as DynamicAlert;
    }

    if (!alert) {
      return;
    }

    return new Promise<DynamicButton>((resolve) => {
      if (alert.type === 'modal') {
        this.cardModalService.show({
          imgPath: alert.image ?? DynamicFormConfig.defaultSuccessAlertAsset,
          title: alert.title,
          content: alert.content,
          buttons: alert.buttons.map((button, index) => {
            return {
              text: button.label,
              handler: () => resolve(button),
              color: index === 0 ? 'guayaba' : 'medianoche-gray',
            };
          }),
        });
      } else {
        this.alertService.show({
          messages: {
            header: alert.title,
            message: alert.content,
            buttons: {
              cancel: alert.buttons[0]?.label,
              accept: alert.buttons[1]?.label ?? undefined,
            },
          },
          handlers: {
            cancel: () => resolve(alert.buttons[0]),
            accept: () => resolve(alert.buttons[1]),
          },
        });
      }
    });
  }

  /**
   * Handle click on dynamic Button clicked
   *
   * @returns The button when the button action can`t be resolved
   */
  public async handleButtonClick(
    button: DynamicButton,
    screen: FlowScreens,
    formId: string
  ): Promise<DynamicButton> {
    switch (button?.action) {
      case ActionType.ALERT:
        return this.showAlertInfo(button.actionInfo?.alert, screen, formId);
      case ActionType.GO_TO_LINK:
        openUrl(button.actionInfo.url);
        break;
      case ActionType.GO_TO:
        this.navController.navigateForward(button.actionInfo.goTo);
        break;
      default:
        return button;
    }
  }

  public showProductList(title: string, productList: BillEntity[]) {
    const itemsProductList = productList.map((item) => {
      return {
        name: item.title,
        description: item.description,
        action: () => {
          this.goToItem(item, [
            DynamicFormConfig.routes.form,
            item.formId,
            DynamicFormConfig.routes.validate,
          ]);
        },
      };
    });

    const optionsBottomSheet: ProductListItem[] = itemsProductList;

    this.dynamicListBottomSheetService.show<ProductListProps>(
      {
        title: title,
        items: optionsBottomSheet,
      },
      ProductListOrganism
    );
  }

  public async showToast(screenConfig: ScreenConfiguration, screenName: string, formId: string) {
    if (!this.showedToasts.includes(screenName)) {
      let buttons = [];

      if (screenConfig.toast.button) {
        buttons = [
          {
            side: 'end',
            text: screenConfig.toast.button.label,
            handler: () =>
              this.handleButtonClick(screenConfig.toast.button, screenName as FlowScreens, formId),
          },
        ];
      }

      this.toastService.show(screenConfig.toast.content, ToastType.INFO, null, ToastPosition.TOP, buttons);
      this.showedToasts.push(screenName);
    }
  }

  private async goToItem(item: ProductEntity | BillEntity, route: string[]) {
    await this.dynamicListBottomSheetService.hide();

    await this.navController.navigateForward(route, {
      state: item,
    });
  }
}
