import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { IonSlides } from '@ionic/angular';
import { DynamicButton, InformativeViewContent } from '@nequi/features/dynamic-form/core';
import { BaseModalOrganism } from '@nequi/ui/base';
import { openUrl } from '@nequi/utils/functions';

@Component({
  selector: 'app-dynamic-informative-view',
  templateUrl: './informative-content.component.html',
  styleUrls: ['./informative-content.component.scss'],
})
export class InformativeContentComponent extends BaseModalOrganism implements OnInit {
  @ViewChild(IonSlides, { static: false }) public slidesComponent: IonSlides;

  @Input() public slides: InformativeViewContent[];
  @Input() public link?: DynamicButton;
  @Input() public button: string;

  public modal: InformativeViewContent;
  public isloaded = false;

  public ngOnInit() {
    if (this.slides.length === 1) {
      this.modal = this.slides[0];
    }
  }

  public onDynamicImageLoad() {
    this.isloaded = true;
  }

  public goBack() {
    this.dismiss();
  }

  public hide() {
    this.dismiss();
  }

  public linkAction() {
    openUrl(this.link.actionInfo.url);
  }
}
